html.color-white {
  .header {
    .lk-user-link {
      &__text {
        color: @blackEye;
      }
    }
  }
}

html.color-black {
  .header {
    .lk-user-link {
      &__text {
        color: @whiteEye;
      }
    }
  }
}

.header {
  .lk-user-link {
    display: flex;
    justify-content: center;
    align-items: center;
    &__text {
      font-size: @font-13;
      text-decoration: underline;
      margin-left: 8px;
      font-weight: 500;
      line-height: 15px;
      color: @colorPrimary3;
    }
    @media (min-width: @hoverW) {
      &:hover {
        .lk-user-link__text {
          text-decoration: none;
        }
      }
    }
  }
}
