html {
  font-size: 14px;
  &.color-white {
    body {
      background: @whiteEye;
    }
  }
  &.color-black {
    body {
      background: @blackEye;
    }
  }
  &.scale-1 {
    font-size: 15px;
  }
  &.scale-2 {
    font-size: 16px;
  }
  &.scale-3 {
    font-size: 17px;
  }
}
